import React,{ Component } from "react";
import { ethers } from "ethers";
import GlobalNavigation from "../../../src/components/GlobalNavigation/GlobalNavigation";
import { connect } from "react-redux";
import "./Organization.css";

const { defaultAbiCoder } = require("ethers").utils;

class Organization extends Component {
  state = {
    address: "",
    signer: null,
    orgAddress: "",
    orgObj: null,
    memberAddress: "",
    members: null,
    isInvidate: false,
    isJoin: false,
    isRemove: false,
  };

  handleSubmit = () => {};

  saveFormData = (event) => {
    const { name, value } = event.target;
    this.setState({
      [name]: value,
    });
    // console.log("amount:" + this.state.amount + "account:" + this.state.account);
  };

  init = async () => {
    const { address, signer } = this.props;
    const response = require("../../constants/abi/Organization.json");
    const abi = response.abi;
    const orgObj = new ethers.Contract(this.state.orgAddress, abi, signer);
    this.setState({address: address, signer: signer, orgObj: orgObj});
  }

  addOrgMembers = async () => {
    await this.init();
    let memberAddrs = [this.state.memberAddress];
    let members = [
        {
            memberType: 1,
            voteRight: 1,
            electionRight: 1,
            beElectedRight: 1,
            proposalRight: 1,
            governanceRight: 0,
            agreeJoinOrg: 0
        }
    ]
    const txResponse = await this.state.orgObj.addOrgMembers(memberAddrs, members);
    console.log(txResponse);
    const txReceipt = await txResponse.wait();
    console.log(txReceipt);
    this.setState({isInvidate: true});
  };

  agreeJoinOrg = async () => {
    await this.init();
    const txResponse = await this.state.orgObj.agreeJoinOrg();
    console.log(txResponse);
    const txReceipt = await txResponse.wait();
    console.log(txReceipt);
    this.setState({isJoin: true});
  };

  delOrgMember = async () => {
    await this.init();
    let memberAddrs = [this.state.memberAddress];
    const txResponse = await this.state.orgObj.delOrgMember(memberAddrs);
    console.log(txResponse);
    const txReceipt = await txResponse.wait();
    console.log(txReceipt);
    this.setState({isRemove: true});
  };

  getOrgMembers = async () => {
    await this.init();
    const members = await this.state.orgObj.getOrgMembers();
    this.setState({members:members});
    console.log("members:", members);
  }

  render() {
    return (
      <div>
        <h1></h1>
        <GlobalNavigation history={this.props.history} />
        <div className="organization-container">
          <form className="form" onSubmit={this.handleSubmit}>
            <div class="input-container">
              <label><span className="font_red">*</span> </label>
              <input
                className="input_text"
                type="text"
                name="orgAddress"
                value={this.state.orgAddress}
                onChange={this.saveFormData}
              />
              </div>
              <br />
              <div class="input-container">
              <label>/</label>
              <input
                className="input_text"
                type="text"
                name="memberAddress"
                value={this.state.memberAddress}
                onChange={this.saveFormData}
              />
            </div>
          </form>
          <button onClick={this.addOrgMembers}></button>
          <div>
            <p>{this.state.isInvidate ? "" + this.state.memberAddress + "" : ""}</p>
          </div>
          <button onClick={this.agreeJoinOrg}></button>
          <div>
            <p>{this.state.isJoin ?"" + this.state.address + ""  : ""}</p>
          </div>
          <button onClick={this.delOrgMember}></button>
          <div>
            <p>{this.state.isRemove ? "" + this.state.memberAddress + "" : ""}</p>
          </div>

          <button onClick={this.getOrgMembers}></button>
          <div>
            <p>{this.state.members == null ? "" : "" + this.state.members}</p>
          </div>
     
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  connected: state.connected,
  address: state.address,
  signer: state.signer,
});

export default connect(mapStateToProps)(Organization);
