import React, { Component } from "react";
import { ethers } from "ethers";
import "./ConnectWallet.css";
import { connect } from "react-redux";
import { CONNECT_WALLET, UPDATE_ADDRESS } from "../../pages/store";

class ConnectWallet extends Component {
  connectWallet = async () => {
    if (window.ethereum) {
      const provider = new ethers.providers.Web3Provider(window.ethereum);
      const signer = await provider.getSigner();
      const accounts = await provider.send("eth_requestAccounts", []);
      const address = accounts[0];
      this.props.connectWallet(address, signer);
      this.props.updateAddress(address, signer);
    } else {
      console.log("Please install MetaMask!");
    }
  };

  render() {
    const { connected, address } = this.props;

    return (
      
      <div className="connect-wallet">
        {connected ? (
          <div className="connect_css_addr">{address}</div>
        ) : (
          <button onClick={this.connectWallet}></button>
        )}
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  connected: state.connected,
  address: state.address,
  signer: state.signer,
});

const mapDispatchToProps = (dispatch) => ({
  connectWallet: (address, signer) =>
    dispatch({ type: CONNECT_WALLET, 
      payload: { address, signer } 
    }),
  updateAddress: (address, signer) =>
    dispatch({
      type: UPDATE_ADDRESS,
      payload: { address: address, signer: signer },
    }),
});

export default connect(mapStateToProps, mapDispatchToProps)(ConnectWallet);
