import React, { Component } from 'react';
import ConnectWallet from '../../../src/components/ConnectWallet/ConnectWallet';

class GlobalNavigation extends Component {
  handleClick = (path) => {
    this.props.history.push(path);
  };

  render() {
    return (
      <div>
        <div className="button-container">
          <button onClick={() => this.handleClick('/')}></button>
          <button onClick={() => this.handleClick('/BallotFactory')}></button>
          <button onClick={() => this.handleClick('/Token')}>Token</button>
          <button onClick={() => this.handleClick('/Ballot')}> </button>
          <button onClick={() => this.handleClick('/Organization')}> </button>
          <ConnectWallet />
        </div>
      </div>
    );
  }
}

export default GlobalNavigation;
