import { useState, useEffect } from "react";
import { ethers } from "ethers";
import React, { Component } from "react";
import { withRouter } from "react-router-dom";
import GlobalNavigation from "../../../src/components/GlobalNavigation/GlobalNavigation";
import ConnectWallet from "../../../src/components/ConnectWallet/ConnectWallet";
import "./Token.css";
import { connect } from "react-redux";

const TokenContractAddress = "0x6e738dB95edF1E0760da14fCeE2294E4Cebf70b9"; //  Token 
const MINTER_ROLE = ethers.utils.keccak256(
  ethers.utils.toUtf8Bytes("MINTER_ROLE")
);

class Token extends Component {
  state = {
    amount: "",
    account: "",
  };

  handleSubmit = () => {};

  saveFormData = (event) => {
    const { name, value } = event.target;
    this.setState({
      [name]: value,
    });
    // console.log("amount:" + this.state.amount + "account:" + this.state.account);
  };

  //Test
  handleClick = () => {
    console.log("Address:", this.props.address);
    console.log("Signer:", this.props.signer);
  };

  mint = async () => {
    const { address, signer } = this.props;
    const response = require("../../constants/abi/Token.json");
    const abi = response.abi;
    const contract = new ethers.Contract(TokenContractAddress, abi, signer);
    const txResponse = await contract.mint(
      this.state.account,
      this.state.amount
    );
    console.log(txResponse);
    const txReceipt = await txResponse.wait();
    console.log(txReceipt);
  };

  render() {
    return (
      <div>
        <h1>Token</h1>
        <GlobalNavigation history={this.props.history} />
        <div className="token-container">
          <form onSubmit={this.handleSubmit}>
            <div class="input-container">
              <label><span className="font_red">*</span> : </label>
              <input
                className="input_text"
                type="text"
                name="account"
                value={this.state.account}
                onChange={this.saveFormData}
              />
            </div>
            <div class="input-container">
              <label><span className="font_red">*</span> </label>
              <input
                className="input_text"
                type="text"
                name="amount"
                value={this.state.amount}
                onChange={this.saveFormData}
              />
            </div>
          </form>
          <button onClick={this.mint}>Token</button>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  connected: state.connected,
  address: state.address,
  signer: state.signer,
});

export default connect(mapStateToProps)(Token);
