import { ethers } from "ethers";
import React, { Component } from "react";
import GlobalNavigation from "../../../src/components/GlobalNavigation/GlobalNavigation";
import { connect } from "react-redux";
import "./Ballot.css";

const { defaultAbiCoder } = require("ethers").utils;
// 
const OrgFactoryAddr = "0x105cDb2F9b744B59690BE6530d2E81511E6013CD";

class Ballot extends Component {
  state = {
    address: "",
    signer: null,
    ballotAddress: "",
    ballotObj: null,
    winner: "",
    p1: "",
    p2: "",
    p3: "",
    voters: "",
    haveVoters: "",
    nonVoters: "",
    admin: "",
    newOrgAddr: "",
  };

  handleSubmit = () => {};

  saveFormData = (event) => {
    const { name, value } = event.target;
    this.setState({
      [name]: value,
    });
    // console.log("amount:" + this.state.amount + "account:" + this.state.account);
  };

  init = async () => {
    const { address, signer } = this.props;
    const response = require("../../constants/abi/Ballot.json");
    const abi = response.abi;
    const ballotObj = new ethers.Contract(this.state.ballotAddress, abi, signer);
    this.setState({address: address, signer: signer, ballotObj: ballotObj});
  }

  vote = async () => {
    await this.init();
    console.log("ballotObj:", this.state.ballotObj);
    const txResponse = await this.state.ballotObj.voting(1);
    console.log(txResponse);
    const txReceipt = await txResponse.wait();
    console.log(txReceipt);

    const event_topic_createOrg = ethers.utils.id("CreateOrg(string,address,address,address)");
    for (let i = 0; i < txReceipt.events.length; i++) {
      let event = txReceipt.events[i];
      if (event.address == OrgFactoryAddr) { //
          let data = event.data;
          let decoded = defaultAbiCoder.decode(
              ["string", "address", "address", "address"],
              data
          );
          let newOrgAddr = decoded[2];
          console.log("", newOrgAddr);
          this.setState({newOrgAddr: newOrgAddr});
      }
    }
  };

  voteResult = async () => {
    await this.init();
    const [winner, p1,p2,p3, voters,haveVoters,nonVoters, admin] = await this.state.ballotObj.getVotingResults();
    this.setState({winner: winner, p1:p1,p2:p2,p3:p3, voters:voters,haveVoters:haveVoters,nonVoters:nonVoters, admin:admin});
    console.log("winner:", winner);
  }

  render() {
    return (
      <div>
        <h1></h1>
        <GlobalNavigation history={this.props.history} />
        <div className="ballot-container">
          <form onSubmit={this.handleSubmit}>
            <div class="input-container">
              <label><span className="font_red">*</span> </label>
              <input
                className="input_text"
                type="text"
                name="ballotAddress"
                value={this.state.ballotAddress}
                onChange={this.saveFormData}
              />
            </div>
          </form>
          <button onClick={this.vote}></button>
          <div>
            <p>{this.state.newOrgAddr == "" ? "" : "" + this.state.newOrgAddr}</p>
          </div>
          <button onClick={this.voteResult}></button>
          { this.state.winner === "" ? "" : (
            <div>
               <p>{this.state.winner == 1 ? "" : (this.state.winner == 2 ? "" : (this.state.winner == 3 ? "": "" ))}</p>
               <p>{this.state.p1.toString()}</p>
               <p>{this.state.p2.toString()}</p>
               <p>{this.state.p3.toString()}</p>
               <p>{this.state.voters}</p>
               <p>{this.state.haveVoters}</p>
               <p>{this.state.nonVoters}</p>
               <p>{this.state.admin} </p>
            </div>
          )}
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  connected: state.connected,
  address: state.address,
  signer: state.signer,
});

export default connect(mapStateToProps)(Ballot);
